import Vue from 'vue'
import vuex from 'vuex'
import axios from 'axios'
import vueResource from 'vue-resource';
Vue.use(vuex)
Vue.use(vueResource)
const store = new vuex.Store({
  state: {
    nbaData: {},
    nbaDataList: {},
    goBackVal: false,
    loadingVal:'',
    findNews:{}
  },
  mutations: {
    haveDatas( state, val ){
      state.nbaData = val
    },
    haveDatasList( state, val ){
      state.nbaDataList = val
    },
    findNewsVal( state,val ){
      state.findNews = val
    }
  },
  actions: {
    getDatas(store, val){
      Vue.http({
        url: 'http://op.juhe.cn/onebox/basketball/nba',
        params: {
          key: 'a8abfa1e921d5f46fac3eff086a30e0a',
        },
        method: 'jsonp'
      }).then(response => {
        store.commit('haveDatas', response.body.result)
        store.commit('haveDatasList', response.body.result.list[0]['tr'])
      }, error => {
        console.log(error);
      })
    },
    findNewsval(store , val){
      axios.get('https://www.juhe.cn/docs/api/id/92/News/248')
        .then(response => {
        store.commit('findNewsVal',response)
      })
        .catch(err => {
        console.log(err);
      })
    }
  },
  getters: {}
})

export default store



