<template>
  <div id="find">
    <div class="header">

      <mt-navbar v-model="selected">
        <mt-tab-item id="1"><span class="aa">NBA</span></mt-tab-item>
        <mt-tab-item id="2"><span class="aa">新闻</span></mt-tab-item>
        <mt-tab-item id="3"><span class="aa">数据</span></mt-tab-item>
        <mt-tab-item id="4"><span class="aa">社区</span></mt-tab-item>
      </mt-navbar>

    </div>
    <div class="router-view">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>

  </div>
</template>
<script>
  import Vue from 'vue'
  import { Navbar, TabItem } from 'mint-ui';

  Vue.component(Navbar.name, Navbar,TabItem.name, TabItem);
  export default{
      data(){
          return{
            nba:false,
            news:false,
            datas:false,
            com:false,
            selected:'1'
          }
      },
    watch:{
          selected(){

              if( this.selected == '1' ){
                this.$router.push({path:'/find/nba'})
                this.selected = '1'
              }else if( this.selected == '2' ){
                  this.$router.push({path:'/find/news'})

              }else if( this.selected == '3' ){
                this.$router.push({path:'/find/datas'})

              }else if( this.selected == '4'){
                  this.$router.push({path:'/find/com'})

              }

          }
    },
    computed:{
      newsVal(){
          return this.$store.state.findNews.data.result
      }
    },
    methods:{

    },

  }
</script>
<style scoped>
  #find{
    padding-top:176px;
  }

  .mint-navbar {
    display: -webkit-box;
    display: -ms-flexbox;
    position: fixed;
    text-align: center;
    top: 80px;
    left: 0;
    width: 100%;
    height: 95px;
    align-items: center;
    border-bottom: 2px solid #ccc;
    background: white;
    z-index: 999;
  }
  .mint-navbar .mint-tab-item {
    padding: 15px 0;
    color: orangered;
  }

  .mint-navbar .mint-tab-item.is-selected {
    border-bottom: 9px solid #26a2ff;
  }



  .mint-navbar .mint-tab-item.is-selected{
    border-bottom: 3px solid orangered;
  }

.aa{
  font-size: 38px;
}
  .router-view{
    padding: 0 15px;
  }
</style>



